import request from '@/utils/request'

/**
 * 登录
 * @param {string} username - 用户名
 * @param {string} password - 密码
 * @return {promise}
 * 如何用axios提交 application/x-www-form-urlencoded 类型请求，可以参考下面这个issue
 * https://github.com/axios/axios/issues/350#issuecomment-227270046
 */
export function loginRequest(corpId, username, password) {
  return request.post(`${R_URL.basic.LOGIN}?corpId=${ corpId }&username=${ username }&password=${ password }`, {
    headers: {
      'Content-Type': 'application/json',
    },
  }).then(res => res.data)
}
