/* eslint-disable vue/return-in-computed-property */
<template>
  <div class="container">
    <div class="content">
      <div class="top">
        <div class="header">
          <span class="title">功倍CRM管理系统</span>
        </div>
      </div>

      <div class="login">
        <a-form @submit="onSubmit" :autoFormCreate="(form) => this.form = form">
          <a-form-item
            fieldDecoratorId="companyId"
            :fieldDecoratorOptions="{
                rules: [{ required: true, message: '请输入公司ID', whitespace: true}]}">
            <a-input size="large" placeholder="公司ID">
              <a-icon slot="prefix" type="user"/>
            </a-input>
          </a-form-item>

          <a-form-item
            fieldDecoratorId="name"
            :fieldDecoratorOptions="{
                rules: [{ required: true, message: '请输入账户名', whitespace: true}]}">
            <a-input size="large" placeholder="账户">
              <a-icon slot="prefix" type="user"/>
            </a-input>
          </a-form-item>
          
          <a-form-item
            fieldDecoratorId="password"
            :fieldDecoratorOptions="{
            rules: [{ required: false, message: '请输入密码', whitespace: true}]}">
            <a-input size="large" placeholder="密码" type="password">
              <a-icon slot="prefix" type="lock"/>
            </a-input>
          </a-form-item>

          <a-form-item>
            <a-button
              :loading="logging"
              style="width: 100%;margin-top: 24px"
              size="large"
              htmlType="submit"
              type="primary"
            >登录</a-button>
          </a-form-item>
        </a-form>
      </div>
    </div>
  </div>
</template>

<script>
import Base64 from 'rishiqing/base64-format'
import { loginRequest } from '../services/basic'

export default {
  name: 'loginPage',
  data() {
    return {
      logging: false,
      checked: false,
    }
  },
  components: { },
  computed: { },
  methods: {
    onSubmit(e) {
      e.preventDefault()
      this.form.validateFields((validErr) => {
        if (!validErr) {
          this.logging = true
          localStorage.removeItem('login_token')
          // 请求
          loginRequest(
            this.form.getFieldValue('companyId'),
            this.form.getFieldValue('name'),
            this.form.getFieldValue('password'),
          ).then((res) => {
            this.logging = false
            if (res.errcode === '0') {
              this.$router.push('/home')
            } else {
              this.$message.warning('公司ID或用户名填写不正确！')
              this.logging = false
            }
          }).catch((errs) => {
              this.$message.warning('公司ID或用户名填写不正确！')
              this.logging = false
          })
        }
      })
    },
  },
  watch: {
  },
  mounted() {
  },
}
</script>

<style lang="scss" scoped>
.container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  overflow: auto;
  background: #f0f2f5
    url("https://gw.alipayobjects.com/zos/rmsportal/TVYTbAXWheQpRcWDaDMu.svg")
    no-repeat center 110px;
  background-size: 100%;
  .content {
    padding: 32px 0;
    flex: 1;
    @media (min-width: 768px) {
      padding: 112px 0 24px;
    }
    .top {
      text-align: center;
      margin-bottom: 50px;
      .header {
        height: 44px;
        line-height: 44px;
        a {
          text-decoration: none;
        }
        .logo {
          height: 44px;
          vertical-align: top;
          margin-right: 16px;
        }
        .title {
          font-size: 33pt;
          color: rgba(0, 0, 0, 0.85);
          font-family: "Microsoft YaHei", sans-serif;
          font-weight: 400;
          position: relative;
          top: 2px;
        }
      }
      .desc {
        font-size: 14px;
        color: rgba(0, 0, 0, 0.45);
        margin-top: 12px;
        margin-bottom: 40px;
      }
    }
    .login {
      width: 368px;
      margin: 0 auto;
      @media screen and (max-width: 576px) {
        width: 95%;
      }
      @media screen and (max-width: 320px) {
        .captcha-button {
          font-size: 14px;
        }
      }
      .icon {
        font-size: 24px;
        color: rgba(0, 0, 0, 0.2);
        margin-left: 16px;
        vertical-align: middle;
        cursor: pointer;
        transition: color 0.3s;

        &:hover {
          color: #1890ff;
        }
      }
    }
  }
}
</style>
